/* Explanation */
.explanation {
  width: 1220px;
  margin: 0 auto;
  margin-bottom: 69px;
}

.explanation__title {
  color: #000000;
  font-family: "Oswald", sans-serif;
  font-size: 60px;
  font-weight: 400;
  line-height: 60px;
  margin-bottom: 32px;
  text-align: left;
}

.explanation__list {
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: space-between;
  list-style: none;
}

.steps-list__item {
  width: 230px;
  height: 280px;
  position: relative;
}

.steps-list__icon {
  position: absolute;
  width: 80px;
  height: 80px;
  top: 47px;
  left: 0;
  margin-bottom: 31px;
}

