  left: 0;
  top: 47px;
  background-color: #68b738;
} */

.steps-list__description {
  color: #444444;
  font-family: "Lato", sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
  position: absolute;
  top: 151px;
  left: 0;
}

.steps-list__number {
  color: #f2f2f2;
  text-align: center;
  font-family: "Oswald", sans-serif;
  font-size: 280px;
  font-weight: 400;
  line-height: 280px;
  position: absolute;
  top: 0;
  right: 0;
  z-index: -1;
}
